import React, { useState, useRef, useEffect } from "react";
import { Link, withRouter } from "react-router-dom";
import { toast } from "react-toastify";
import Helmet from "react-helmet";
import simpleReactValidator from "simple-react-validator";
import { loginUser } from "../services/userServices";
import Loader from "react-loader-spinner";
import loginIcon from "./img/Login-icon.png";

const Login = ({ history }) => {
  const [username, setUsername] = useState("");
  const [, forceUpdate] = useState("");
  const [isLoading, setLoading] = useState(false);

  const validator = useRef(
    new simpleReactValidator({
      messages: {
        required: "Please fill out this field.",
      },
      element: (message) => <div className="invalid-field">{message}</div>,
    })
  );

  //* Remove all local storages in loading
  useEffect(() => {
    localStorage.removeItem("userId");
    localStorage.removeItem("fullname");
  }, []);

  //---------------------------------------------
  //Call login API
  //Using local API
  //output parameter: status, data
  //status:200 => User login successfully
  //status:204 => User did not found.
  //---------------------------------------------
  const login = async (e) => {
    e.preventDefault();
    try {
      setLoading(true);
      if (validator.current.allValid()) {
        const user = { username };
        const { status, data } = await loginUser(user);

        if (status === 200) {
          /// toast.success("User login successfully");
          localStorage.setItem("userId", data.user._id);
          localStorage.setItem("fullname", data.user.fullname);
          history.replace("/search");
        } else if (status === 204) {
          setLoading(false);
          toast.error("User did not found.");
        }
      } else {
        setLoading(false);
        validator.current.showMessages();
        forceUpdate(1);
      }
    } catch (error) {
      setLoading(false);

      if (error.response) {
        toast.error(`${error.response.data.message}`);
      } else {
        toast.error("There is a Problem.");
        if (process.env.NODE_ENV === "development") console.log(error);
      }
    }
  };

  return (
    <div className="main">
      <Helmet>
        <title>Simple Search | Login</title>
      </Helmet>

      {isLoading ? (
        <div className="loader">
          <Loader type="Bars" color="#9933cc" height={80} width={80} />
          <h5>Please wait ...</h5>
        </div>
      ) : (
        <div className="card">
          <div className="card-header">
            <h1>Login</h1>
          </div>
          <div className="card-body">
            <div className="img-wrapper">
              <img src={loginIcon} alt="user icon" className="form-image" />
            </div>
            <div className="form-wrapper">
              <form onSubmit={login} className="login-form">
                <input
                  id="inputUsername"
                  name="username"
                  type="text"
                  placeholder="Username"
                  className="form-input"
                  autoFocus
                  value={username}
                  onChange={(e) => {
                    setUsername(e.target.value);
                    validator.current.showMessageFor("username");
                  }}
                />
                {validator.current.message("username", username, "required")}

                <button type="submit" className="form-button">
                  Login
                </button>
              </form>
              <div className="link-wrapper">
                <Link to="/register" className="link">
                  Create new user.
                </Link>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default withRouter(Login);
