import smallsearchImage from "./img/small-search.png";
import clockImage from "./img/clock2.png";

const SearchSuggestions = ({
  filteredSuggestions,
  activeSuggestion,
  handleClick,
  input,
  history,
}) => {
  return (
    <>
      {filteredSuggestions.length ? (
        <ul className="suggestions">
          {filteredSuggestions.map((suggestion, index) => {
            let className;

            // control active suggestion and set the class
            if (index === activeSuggestion) {
              className = "suggestion-active";
            }

            return (
              <li
                className={className}
                key={index}
                onClick={() => {
                  !history
                    ? handleClick(suggestion.word)
                    : handleClick(suggestion.searchedPhrase);
                }}
              >
                {!history ? (
                  /* Make Suggestions list */
                  suggestion.word.substr(0, input.length) === input ? (
                    <div className="li-flex">
                      <img
                        src={smallsearchImage}
                        alt="clock"
                        className="small-img"
                      />
                      <span>
                        <span className="suggestion-text">
                          {suggestion.word.substr(0, input.length)}
                        </span>
                        {suggestion.word.substr(
                          input.length,
                          suggestion.word.length
                        )}
                      </span>
                    </div>
                  ) : (
                    <div className="li-flex">
                      <img
                        src={smallsearchImage}
                        alt="clock"
                        className="small-img"
                      />
                      {suggestion.word}
                    </div>
                  )
                ) : (
                  /* Make 10 top searched history list */
                  <div className="li-flex">
                    <img src={clockImage} alt="clock" className="small-img" />
                    {suggestion.searchedPhrase}
                  </div>
                )}
              </li>
            );
          })}
        </ul>
      ) : null}
    </>
  );
};

export default SearchSuggestions;
