import { localServer } from "./server";

export const addSuggestions = (suggesstion) => {
  return localServer.post("/search/add", JSON.stringify(suggesstion));
};

export const deleteSearchItem = (historyID) => {
  return localServer.get("/search/delete/" + historyID);
};

export const getSearchHistory = () => {
  return localServer.get(`/search/${localStorage.getItem("userId")}`);
};

export const deleteAllSearchItems = () => {
  return localServer.get(
    `/search/delete-all/${localStorage.getItem("userId")}`
  );
};
