import React, { useState, useRef } from "react";
import { withRouter } from "react-router-dom";
import simpleReactValidator from "simple-react-validator";
import Helmet from "react-helmet";

import { addNewUser } from "../services/userServices";
import { toast } from "react-toastify";
import loginIcon from "./img/user.png";

const Newuser = ({ history }) => {
  const [fullname, setFullname] = useState("");
  const [username, setUsername] = useState("");
  const [, forceUpdate] = useState();

  const validator = useRef(
    new simpleReactValidator({
      messages: {
        required: "Please fill out this field.",
      },
      element: (message) => <div className="invalid-field">{message}</div>,
    })
  );

  //* Clear form
  const resetForm = () => {
    setFullname("");
    setUsername("");
  };

  //---------------------------------------------
  //Register new user
  //Using local API
  //Input parameter: fullname, username
  //Output parameter: status
  //status:201 => Successful insert
  //---------------------------------------------
  const register = async (e) => {
    e.preventDefault();
    const newUser = { fullname, username };
    try {
      if (validator.current.allValid()) {
        const { status } = await addNewUser(newUser);
        if (status === 201) {
          toast.success("User has been added successfully.");
          resetForm();
          history.replace("/suggestion");
        }
      } else {
        validator.current.showMessages();
        forceUpdate(1);
      }
    } catch (error) {
      toast.error("There is a Problem.");
      if (process.env.NODE_ENV === "development") console.log(error);
    }
  };

  //* Log out user and redirect to login page
  const homeRedirectOnClick = () => {
    history.replace("/suggestion");
  };

  return (
    <div className="main">
      <Helmet>
        <title>Simple Search | New User</title>
      </Helmet>
      <div className="card">
        <div className="card-header">
          {" "}
          <h1>Add new user</h1>
        </div>
        <div className="card-body">
          <div className="img-wrapper">
            <img src={loginIcon} alt="user icon" className="form-image" />
          </div>
          <form onSubmit={register} className="login-form">
            <input
              id="inputUsername"
              name="username"
              type="text"
              placeholder="Username"
              className="form-input"
              autoFocus
              value={username}
              onChange={(e) => {
                setUsername(e.target.value);
                validator.current.showMessageFor("username");
              }}
            />
            {validator.current.message("username", username, "required")}

            <input
              id="inputFullname"
              name="fullname"
              type="text"
              placeholder="Fullname"
              className="form-input"
              value={fullname}
              onChange={(e) => {
                setFullname(e.target.value);
                validator.current.showMessageFor("fullname");
              }}
            />
            {validator.current.message("fullname", fullname, "required")}

            <button type="submit" className="form-button">
              Register
            </button>
            <button
              className="form-button home-button"
              onClick={homeRedirectOnClick}
            >
              Home
            </button>
          </form>
        </div>
      </div>
    </div>
  );
};

export default withRouter(Newuser);
