import { useState } from "react";

import data from "../data/historyTabsData.json";
import SearchHistory from "./SearchHistory";

const SearchHistoryMain = ({
  searchHistoryList,
  handleRemoveHistoryItem,
  handleDeleteHistory,
}) => {
  const [active, setActive] = useState(0);
  var today = new Date();
  var tempDate = new Date();
  var tempList = [];

  return (
    <div className="search-history">
      <div className="search-history-warapper">
        <h4 className="search-history-caption">Search history</h4>
        <h4 className="search-history-clear" onClick={handleDeleteHistory}>
          Clear search history
        </h4>
      </div>
      {data.map((item, index) => {
        switch (item.aim) {
          case "TODAY":
            tempList = searchHistoryList.filter(
              (historyItem) =>
                new Date(historyItem.searchedAt).toDateString() ===
                today.toDateString()
            );
            break;
          case "YESTERDAY":
            tempDate.setDate(today.getDate() - 1);
            tempList = searchHistoryList.filter(
              (historyItem) =>
                new Date(historyItem.searchedAt).toDateString() ===
                tempDate.toDateString()
            );
            break;
          case "WEEK":
            ///console.log(today.toISOString().substr(0, 10));

            var fromData = new Date();
            var toDate = new Date();
            fromData.setDate(today.getDate() - 1);
            toDate.setDate(fromData.getDate() - 8);
            //console.log(fromData.setHours(0, 0, 0, 0));

            tempList = searchHistoryList.filter(
              (historyItem) =>
                new Date(historyItem.searchedAt).setHours(0, 0, 0, 0) <
                  fromData.setHours(0, 0, 0, 0) &&
                new Date(historyItem.searchedAt).setHours(0, 0, 0, 0) >
                  toDate.setHours(0, 0, 0, 0)
            );
            break;
          case "OLDER":
            tempDate.setDate(today.getDate() - 9);
            tempList = searchHistoryList.filter(
              (historyItem) =>
                new Date(historyItem.searchedAt).setHours(0, 0, 0, 0) <
                tempDate.setHours(0, 0, 0, 0)
            );
            break;
          default:
            break;
        }
        return (
          <div key={index}>
            <button
              className={
                active === index
                  ? "collapsible  active-collapsible"
                  : "collapsible"
              }
              onClick={() => setActive(index)}
            >
              {item.title}
            </button>
            <div
              className={
                active === index ? "content-show active" : "content-hide"
              }
            >
              <SearchHistory
                searchHistoryList={tempList}
                handleRemoveHistoryItem={handleRemoveHistoryItem}
              />
            </div>
          </div>
        );
      })}
    </div>
  );
};

export default SearchHistoryMain;
