import { localServer } from "./server";

export const addNewUser = (newUser) => {
  return localServer.post("/user/add", JSON.stringify(newUser));
};

export const loginUser = (user) => {
  return localServer.post("/user/login", JSON.stringify(user));
};

export const userSetting = () => {
  return localServer.get(`/user/setting/${localStorage.getItem("userId")}`);
};

export const saveSetting = (setting) => {
  return localServer.post("/user/setting-update", JSON.stringify(setting));
};
