import Moment from "react-moment";
import clockImage from "./img/clock2.png";

const SearchHistory = ({ searchHistoryList, handleRemoveHistoryItem }) => {
  return (
    <ul className="ul-history">
      {searchHistoryList.length > 0 ? (
        searchHistoryList.map((suggestion) => (
          <li className="li-history" key={suggestion._id}>
            <img src={clockImage} alt="clock" className="small-img" />
            {suggestion.searchedPhrase}
            <p className="search-date">
              <Moment
                date={suggestion.searchedAt}
                format="YYYY-MM-DD h:mm A"
              ></Moment>
            </p>
            <span
              className="delete-button"
              onClick={() => handleRemoveHistoryItem(suggestion._id)}
            >
              &times;
            </span>
          </li>
        ))
      ) : (
        <h4 className="ul-nodata">There is not any data.</h4>
      )}
    </ul>
  );
};

export default SearchHistory;
